        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item
            v-for="(item, index) in crumbs"
            :key="index"
            :to="{name: item.name}"
            :class="{'can-click': item.name && index !== crumbs.length - 1}"
          >{{item.value}}</el-breadcrumb-item>
        </el-breadcrumb>
        <el-dropdown trigger="click">
          <span class="el-dropdown-link">
            <span class="svg-container svg-container_login">
              <svg-icon name="user-settings" style="font-size: 2em;"/>
            </span>
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item>{{user.username}}</el-dropdown-item>
            <el-dropdown-item @click.native.stop="logout">退出</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
        <router-link class="msg-icon" :to="{name: 'message-list'}">
          <b v-show="hasmsg">{{unReadMsgNumber}}</b>
        </router-link>
        <router-link class="el-icon-setting icon-setting" :to="{name: 'sample-list'}"></router-link>
        <a class="btn-back" href="#" @click.stop.prevent="goPrevPage">返回上一页</a>
      </div>
      <div class="main">
        <router-view/>
      </div>
    </div>
  </div>
</template>

<script>
import {getNoreadCount} from '@/api/message.js';
import {mapGetters} from 'vuex';

export default {
  name: 'App',
  data() {
    return {
      activeIndex: '',
      crumbs: [],
      hasmsg: false,
      totalmsg: ''
    };
  },
  computed: {
    ...mapGetters(['unReadMsgNumber', 'user'])
  },
  watch: {
    $route: {
      handler: function(to, from) {
        this.activeIndex = to.path;
        this.fromPage = from;
        this.crumbs = to.name !== 'pagenotfind' ? to.meta.crumbs : [];
        this.getLatestMsg();
      },
      immediate: true
    }
  },
  methods: {
    goPrevPage() {
      if (this.fromPage.name === 'monitor-list' && this.activeIndex === '/univariate-monitor') {
        this.$router.push({path: 'monitor-list', query: this.$store.state.monitorListParams});
      } else {
        this.$router.go(-1);
      }
    },
    getLatestMsg() {
      // 获取未读信息数
      getNoreadCount()
        .then(result => {
          if (result.result > 0) {
            this.hasmsg = true;
            this.$store.dispatch('getUnReadMsgNumber', {
              unReadMsgNumber: result.result
            });
          } else {
            this.hasmsg = false;
            return;
          }
        })
        .catch(err => console.log(err));
    },
    logout() {
      const token = this.user.token;
      this.$store
        .dispatch('LOGOUT', token)
        .then(result => {
          this.$router.push({path: '/login'});
        })
        .catch(err => {
          console.log(err);
        });
    },
    gotorc() {
      const token = this.user.token;

      this.$store
        .dispatch('getRCUrl', token)
        .then(result => {
          // if (process.env.NODE_ENV !== 'production') {
          //   window.location.href =
          //     'http://localhost:18509/Home/Index?account=' + result.data;
          // } else {
          //   window.location.href =
          //     'http://10.1.21.82:8000/Home/Index?account=' + result.data;
          // }
          console.log(result.result);
          window.open(result.result);
        })
        .catch(err => {
          console.log(err);
        });
    }
  },
  created() {
    // this.activeIndex = this.$route.path;
    this.crumbs = this.$route.meta ? this.$route.meta.crumbs : [];
    // 统一获取所有产品列表
    this.$store.dispatch('getRCProductList');
  },
  mounted() {
    this.getLatestMsg();
  }
};
</script>

